import React,  { useState } from 'react'

export default function TextForm(props) {
    const handleUpClick = () =>{
        console.log("Upper case was clicked" +text)
        setText("you have clicked on handleupclick")
        let newText = text.toUpperCase();
        setText(newText )
    }
    
    const handleLoClick = () =>{
        console.log("lower case was clicked" +text)
        setText("you have clicked on handleupclick")
        let newText = text.toLowerCase();
        setText(newText )
    }


    
    const handleremoveClick = () =>{
        console.log("you have clicked to clear text" + text)
        window.alert('text will get clear')
        let newText = ' ';
        setText(newText )
    }

    const handleOnChange =(event) => {
        console.log("On change");
        setText(event.target.value)
    }

    
    const [text, setText] = useState('enter your text');
    
    return (
        <>
        <div className="container ">
            <h1>{props.heading}</h1>
            <div classname="mb-3">
 
  <textarea className="form-control" value={text} onChange ={handleOnChange} id="mybox" rows="8"></textarea>
</div>

<button  className="btn btn-dark mx-3 my-2" onClick = {handleUpClick}> Convert to upper case </button> 
<button  className="btn btn-dark mx-3 my-2 " onClick = {handleLoClick}> Convert to lower case </button>  
<button className="btn btn-dark mx-3 my-2"  onClick={handleremoveClick}>Clear text</button>

        </div>
        <div className="container my-3">
            <h1>your text summary </h1>
            <p>{text.split(" ").length} words ,{text.length} characters</p>
            <p>Average time to read by human : {0.008*text.split(" ").length } minutes</p>
            <h2>Preview</h2>
            <p>{text}</p>

        </div>
     </>
    )
}
